<template>
  <div>
    <div v-if="isInstall">
      <div>
        에러 : {{errorMessage}}
      </div>
      
      <div>        
        <div v-if="address == null">
          <button v-on:click="connectMetaMask">메타마스크 연결</button>
        </div>        
        <div v-else>
          현재 계정 : {{address}}            

          <div v-if="connectedContract == null">
            <button v-on:click="connectContract">컨트랙트 연결</button>
          </div>
          <div v-else>
            <!-- 컨트랙트가 연결 되었다! -->

            <!-- balanceOf -->
            <div>
              <button v-on:click="balanceOf">balanceOf 호출</button>
              <input type="text" v-model="balanceOf_address" placeholder="조회할 지갑 주소">  
              <div v-if="balanceOf_result != null">
                balanceOf 결과 : {{balanceOf_result}}
              </div>            
            </div>
            
            <div>
              <button v-on:click="mint">mint 호출</button>
              <input type="text" v-model="mint_to" placeholder="to">  
              <input type="text" v-model="mint_tokenId" placeholder="tokenId">  
              <input type="text" v-model="mint_uri" placeholder="uri">  
              <div v-if="mint_result != null">
                mint 결과 : {{mint_result}}
              </div>            
            </div>

          </div>
        </div>
      </div>
    </div>
    <div v-else>
      메타 마스크 설치 안됨!
    </div>
  </div>
</template>

<script>
import {ethers} from 'ethers';
import abi from '../assets/abi.json'

export default 
{
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      isInstall : window.ethereum != null && window.ethereum.isMetaMask,
      address : null,
      connectedContract : null,
      errorMessage : "",
      
      // balanceOf
      balanceOf_address : null,
      balanceOf_result : null,

      // mint
      mint_to : null,
      mint_tokenId : null,
      mint_uri : null,
      mint_result : null,
    }
  },
  methods:{
    async connectMetaMask()
    {
      console.log('connectMetaMask 호출');
      const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
      console.log(accounts);

      if ( accounts > 0 )
      {
        this.address = accounts[0];
      }
    },

    async connectContract()
    {
      console.log('connectContract 호출');
      var contractAddress = '0x0c350d6A8fb6eE905291B088F43e225256e2834D';
      
      var etherProvider = new ethers.providers.Web3Provider(window.ethereum);
      let tempSigner = etherProvider.getSigner();
      this.connectedContract = new ethers.Contract(contractAddress, abi, tempSigner);
      console.log(this.connectedContract)
    },

    async balanceOf()
    {
      console.log('balanceOf 호출');
      var value = await this.connectedContract.balanceOf(this.balanceOf_address);
      this.balanceOf_result = value;
    },

    async mint()
    {
      console.log('mint 호출');
      console.log(this.mint_to);
      console.log(this.mint_tokenId);
      console.log(this.mint_uri);
      try
      {
        var transaction = await this.connectedContract.mint(this.mint_to, this.mint_tokenId, this.mint_uri);
        this.mint_result = "대기중...";
        await transaction.wait();
        this.mint_result = transaction;
      }
      catch(e)
      {
        this.errorMessage = e.message;
      }
    },

  }
}

</script>

